export default {
	data() {
		return {
			title: 'Hello',
			disabled: false,
			currentTime: 60,   // 倒计时初始值
			time: '获取验证码',
			formNode:{
				mobile: '',       // 手机号
				code: '',          // 验证码
				password: '',     //密码
				verification_key: '', //
				password1: ''
			}
		}
	},
	onLoad() {
		console.log(this.title)
	},
	methods: {
		getLoginName(e) {
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
			// this.loginName = e.detail.value
			console.log(this.formNode)
			
		},
		// 获取验证码
		getCode(){
			if (this.disabled) {
				return false
			}
			if (this.formNode.mobile == '') {
				wx.showToast({
					title: "请输入手机号",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.mobile.length != 11) {
				wx.showToast({
					title: "请输入正确的手机号",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			uni.showToast({
				title: "请求发送中...",
				icon: 'loading',
				duration: 1000
			});
			this.disabled = true
			console.log(getApp().globalData.requestUrl)
			uni.request({
			    url: `${getApp().globalData.requestUrl}/send_sms`, //仅为示例，并非真实接口地址。
				method: 'POST',
			    data: {
					mobile: this.formNode.mobile
				},
			    success: (res) => {
			        console.log(res);
					if(res.statusCode == 200){
						this.formNode.verification_key = res.data.key
						this.countdown();
					} else{
						uni.showToast({
							title: "手机号有误",
						});
					}
					
			    }
			});
		},
		//倒计时
		countdown(){
			var currentTime = this.currentTime;
			this.time = `倒计时${currentTime}秒`
			var interval = setInterval(() => {
				this.time = '倒计时' + (currentTime - 1) + '秒'
				currentTime--
				if (currentTime <= 0) {
					clearInterval(interval)
					this.time = '重新获取'
					this.currentTime = 60
					this.disabled = false
				}
			}, 1000)
		},
		//立即注册
		login(){
			if (this.formNode.mobile == '') {
				wx.showToast({
					title: "请输入手机号",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.mobile.length != 11) {
				wx.showToast({
					title: "请输入正确的手机号",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.code == '') {
				wx.showToast({
					title: "请输入验证码",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.password == '') {
				wx.showToast({
					title: "请输入密码",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.password1 == '') {
				wx.showToast({
					title: "请确认密码",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if (this.formNode.password != this.formNode.password1) {
				wx.showToast({
					title: "请输入密码一致",
					icon: 'none',
					duration: 2000
				});
				return false
			}
			uni.showToast({
				title: "注册中...",
				icon: 'loading',
				duration: 1000
			})
			uni.request({
			    url: `${getApp().globalData.requestUrl}/register`, //仅为示例，并非真实接口地址。
				method: 'POST',
			    data: this.formNode,
			    success: (res) => {
			        console.log(res);
					if(res.data.status_code == 1){
						uni.showToast({
							title: res.data.message,
						});
						uni.reLaunch({
						    url: './index'
						});
					} else{
						uni.showToast({
							title: res.data.message,
						});
					}
					
			    }
			});
		}
	},
}
